import React, { useEffect, useState } from 'react';
import Axios from "axios";


export const Test = () => {
    const [parseFile, setParseFile] = useState([]);
    const axios = Axios.create();

    // useEffect (() => {

    // });
    // auth: {
    //     username: 'username',
    //     password: 'password'
    //   },
    const testToken = () => {
        axios.interceptors.response.use((response) => {
            if (response) {
                console.log(response.drinks)
            }
        }, 
        (error) => {
            console.log(error);
        });
        axios.get("https://www.thecocktaildb.com/api/json/v1/1/filter.php?i=Vodka", {
            headers: {
                'Access-Control-Allow-Origin': '*', // * или ваш домен
                'Access-Control-Allow-Methods': 'GET, PUT, POST, DELETE',
                'Access-Control-Allow-Headers': 'Origin, X-Requested-With, Content-Type, Accept'
            }
        });
    }

    const testRq = () => {
        fetch('https://www.thecocktaildb.com/api/json/v1/1/filter.php?i=Vodka')
        .then(res => res.json()) 
        .then(
            (result) => {
                setParseFile(result.drinks)
            },
            (error) => {
                console.log(error);
            }
        )
    }

    

    return (
        <>
        <input type="submit"  onClick={testToken} value="Token"/>
        <input type="submit"  onClick={testRq}/>
        <code>
            <ul>
                {parseFile.map(item => (
                    <li className="ParseList" key={item.idDrink}> 
                    {item.strDrink}
                    <img className="ParseImg" src={item.strDrinkThumb} alt={item.idDrink}/>
                    </li>
                ))}
            </ul>
            </code>
        </>

    )
}
